<template>
  <div>
    <LoHeader class="lo_header" @query="queryData" :FilterItems="FilterItems" title="北部彩赔率设置"></LoHeader>
    <mu-container ref="container" class="lo_content_box">
      <mu-load-more class="lo_content" :refreshing="refreshing" :loading="loading" style="width: 100%;"
                    @refresh="refresh" @load="load">
        <template v-for="item in tableData">
          <div>
            <div class="container">
              <div class="desc">
                <div class="title">
                  <div class="title_1">类型：{{item.OddsDes}}</div>
                  <div class="title_1">赔率：{{item.Odds}}</div>
                  <div class="btn">
                    <div class="btn_01" @click="openDialog(item)">修改</div>
                  </div>
                </div>
                <div class="priceDiv">
                  <div>
                    <span class="label">全上限：</span>
                    <span class="price">{{item.AllUserMaxBet}}</span>
                  </div>
                  <div>
                    <span class="label">单上限：</span>
                    <span class="price">{{item.OneUserMaxBet}}</span>
                  </div>
                  <div>
                    <span class="label">单下限：</span>
                    <span class="price">{{item.OneUserMinBet}}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </template>
      </mu-load-more>
    </mu-container>

    <mu-dialog width="360" transition="slide-bottom" fullscreen :open.sync="openFullscreen">
      <mu-appbar color="primary" title="修改赔率">
        <mu-button slot="left" icon @click="closeDialog">
          <mu-icon value="close"></mu-icon>
        </mu-button>
        <mu-button slot="right" flat  @click="saveModify">
          保存
        </mu-button>
      </mu-appbar>
      <div style="padding: 24px;">
        <mu-form ref="form" :model="formInfo.OneOddsInfo" class="mu-demo-form">
          <mu-form-item label="赔率名称" prop="OddsDes"  >
            <mu-text-field disabled v-model="formInfo.OneOddsInfo.OddsDes" prop="OddsDes"></mu-text-field>
          </mu-form-item>

          <mu-form-item label="赔率"  prop="Odds"  >
            <mu-text-field type="number" v-model="formInfo.OneOddsInfo.Odds" prop="Odds"></mu-text-field>
          </mu-form-item>
          <mu-form-item label="单上限"  prop="OneUserMaxBet"  >
            <mu-text-field type="number" v-model="formInfo.OneOddsInfo.OneUserMaxBet" prop="OneUserMaxBet"></mu-text-field>
          </mu-form-item>
          <mu-form-item label="单下限"  prop="OneUserMinBet"  >
            <mu-text-field type="number" v-model="formInfo.OneOddsInfo.OneUserMinBet" prop="OneUserMinBet"></mu-text-field>
          </mu-form-item>
          <mu-form-item label="全下限"  prop="AllUserMaxBet"  >
            <mu-text-field type="number" v-model="formInfo.OneOddsInfo.AllUserMaxBet" prop="AllUserMaxBet"></mu-text-field>
          </mu-form-item>
        </mu-form>
      </div>
    </mu-dialog>
  </div>
</template>

<script>
import LoHeader from "../../components/LoHeader";
import {GetOddsListFirstId,SaveOddsInfo} from '../../api/OddsInfo';

export default {
  name: "Wsx2BbcOddsInfoSet",
  components: {
    LoHeader
  },
  data () {
    return {
      num: 10,
      refreshing: false,
      loading: false,
      FilterItems:[
        {label:'赔率名:',Value:'',type:"text",placeholder:"赔率名",icon:"",Name:"OddsDes"},
        {label:'赔率Key:',Value:'',type:"text",placeholder:"赔率Key",icon:"",Name:"OddsType"},
        {label:'赔率分类:',Value:0,type:"select",placeholder:"赔率分类",icon:"",Name:"BigType",options:this.Config.Wsx1NBigTypeOptions},
      ],

      PageTotal:0,
      tableData: [],
      GroupData:{},
      query: {
        OddsDes:'',
        pageIndex: 1,
        OddsType:0,
        BigType:0,
        pageSize: 10,
        GameType:this.Config.GameType.Wsx_202Bbc,
        FirstId:0,
      },

      openFullscreen:false,
      formInfo:{
        OldData:{},
        OneOddsInfo:{Id:0,OddsDes:'',Odds:1,OneUserMaxBet:99999,OneUserMinBet:0,AllUserMaxBet:999999},
        // Rules:{
        //   OddsRules:[
        //     { validate: (val) => val<=0, message: '赔率必须大于0' }
        //   ],
        // }
      },

    }
  },
  methods: {
    refresh () {
      this.refreshing = true;
      this.query.pageIndex = 1;
      this.query.FirstId = 0;
      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
        this.refreshing = false;
      });
    },
    load() {
      this.loading = true;
      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
        this.loading = false;
      });
    },

    getData(fun){
      GetOddsListFirstId(this.query).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          if (this.query.FirstId==0) {
            this.tableData = obj.ListData;
          } else {
            this.tableData = this.tableData.concat(obj.ListData);
          }
          this.GroupData = obj.GroupData;
          this.PageTotal = obj.PageTotal;
          this.query.FirstId = obj.GroupData.FirstId;

          fun(true);
          resolve();
        } else {
          this.$toast.error(msg);
          fun(false);
        }
      }).catch(error => {
        fun(false);
      });
    },
    openDialog(item){
      this.OldData = item;
      this.formInfo.OneOddsInfo.Id = item.Id;
      this.formInfo.OneOddsInfo.OddsDes = item.OddsDes;
      this.formInfo.OneOddsInfo.Odds = item.Odds;
      this.formInfo.OneOddsInfo.OneUserMaxBet = item.OneUserMaxBet;
      this.formInfo.OneOddsInfo.OneUserMinBet = item.OneUserMinBet;
      this.formInfo.OneOddsInfo.AllUserMaxBet = item.AllUserMaxBet;

      this.openFullscreen = true;
    },
    closeDialog () {
      this.openFullscreen = false;
    },
    saveModify(){
      SaveOddsInfo(this.formInfo.OneOddsInfo).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          this.OldData.OddsDes = this.formInfo.OneOddsInfo.OddsDes;
          this.OldData.Odds = this.formInfo.OneOddsInfo.Odds;
          this.OldData.OneUserMaxBet = this.formInfo.OneOddsInfo.OneUserMaxBet;
          this.OldData.OneUserMinBet = this.formInfo.OneOddsInfo.OneUserMinBet;
          this.OldData.AllUserMaxBet = this.formInfo.OneOddsInfo.AllUserMaxBet;

          this.closeDialog()
          resolve();
        } else {
          this.$toast.error(msg);
        }
      }).catch(error => {
      });

    },

    queryData(filterItems){
      this.query.pageIndex = 1;
      this.query.FirstId = 0;
      this.query.userName = '';
      this.query.userId =0;
      this.query.beginDay = '';
      this.query.endDay = '';

      for (let key in filterItems) {
        if (filterItems[key].Name === 'OddsDes') {
          this.query.OddsDes = filterItems[key].Value;
        } else if (filterItems[key].Name === 'OddsType') {
          this.query.OddsType = filterItems[key].Value;
        } else if (filterItems[key].Name === 'BigType') {
          this.query.BigType = filterItems[key].Value;
        }
      }

      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
      });
    },

  },
  created() {
    this.query.pageIndex = 1;
    this.query.FirstId = 0;
    this.getData((v)=>{
      if (v==true){
        this.query.pageIndex = this.query.pageIndex+1
      }
    });
  },
}
</script>

<style scoped>
@import url('../../assets/css/lottery_num.css');
@import url('../../assets/css/main.css');

.container {
  width: 100%;
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #F2F2F2;
  justify-content: space-between;
  padding: 5px 11px;
  margin: 0 0 10px;
  background: #fff;
}
.desc {
  color: #8B8B8B;
  padding: 2px;
  text-align: left;
  font-size: 12px;
  width: 100%;
}
.title {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.title_1 {
  font-weight: bold;
  color: #333;
  overflow: hidden;
  font-size: 14px;
}
.price {
  color: red;
  font-size: 12px;
  font-weight: 900;
}
.fsid {
  color: #8B8B8B;
}

.priceDiv {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.xiaol {
  color: #8B8B8B;
  color: red;
}
.kc {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.btn_01 {
  border: 1px solid #8B8B8B;
  border-radius: 3px;
  text-align: center;
  margin-left: 10px;
  padding: 3px 8px;
}
.btn {
  display: flex;
  justify-content: flex-end;
}
.main {
  margin-top: 8px;
  width: 100%;
  /*margin-top: 1.4rem;*/
  margin-bottom: 1.4rem;
}
.label {

}

</style>



